<template>
    <div class="fix-er">
        <ul>
            <li>
                <span>微信客服</span>
                <div class="er-code-wrap">
                    <img src="/static/image/ercode/customer-service.jpg" alt="">
                </div>
            </li>
            <li>
                <span>公众号</span>
                <div class="er-code-wrap">
                    <img src="/static/image/ercode/ercode.jpg" alt="">
                </div>
            </li>
            <li>
                <span>商务合作</span>
                <div class="er-code-wrap">
                    <img src="/static/image/ercode/wechat.jpg" alt="">
                </div>
            </li>
            <li class="back-top" @click="toTop">
                <img src="/static/image/icon/btt.png" alt="">
            </li>
        </ul>
    </div>
</template>
<script>
    export default{
        data:()=>({

        }),
        methods:{
            toTop(){
                window.backToTop(4)
            }
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
    .fix-er{
        position: fixed;
        width: 200px;
        height: 270px;
        /*background-color: rgba(0,0,0,.5);*/
        left:50%;
        bottom: 420px;
        transform: translateX(530px);
        /*z-index: 99;*/
        >ul{
            width: 88px;
            height: 270px;
            border-radius: 11px;
            background-color: #ffc207;
            border: solid 2px #d89931;
            >li{
                position: relative;
                height: 69px;
                line-height: 69px;
                font-size: 14px;
                font-weight: 600;
                text-align: center;
                color: #4a4a4a;
                cursor: pointer;
                border-bottom: solid 1px #d99930;
                >.er-code-wrap{
                    display: none;
                    position: absolute;
                    top:0;
                    left:100px;
                    >img{
                        width: 100px;
                        height: 100px;
                    }
                }
                &.back-top{
                    height: 60px;
                    border: none;
                    img{
                        width: 88px;
                        height: 60px;
                    }
                }
                &:hover{
                    >.er-code-wrap{
                        display: block;
                    }
                }
            }
        }
    }
</style>


